import './App.css';
import useFitText from "use-fit-text";
import axios from 'axios';


var fonts = ['Roboto',
  'Open_Sans',
  'Raleway',
  'Rakkas',
  'Abril_Fatface',
  'Berkshire_Swash',
  'Arvo',
  'Spectral',
  'Germania_one'
]

function App() {
  return (
    <div className="App">
      <link href="https://fonts.googleapis.com/css?family=Open+Sans|Raleway|Roboto|Rakkas|Abril+Fatface|Berkshire+Swash|Arvo|Spectral|Germania+One" rel="stylesheet">
      </link>
      <header className="App-header">

        <div class="text">

          <h1 id="apology">Press spacebar to apologize</h1>

        </div>

      </header>
    </div>
  );
}

function getHex() {
  return '#' + Math.random().toString(16).slice(2, 8);
}


function setBackground() {
  var bgColor = getHex();
  document.body.style.background = bgColor;
}


document.body.onkeyup = function (a) {
  if (a.keyCode === 32) {

    var rand = fonts[Math.floor(Math.random() * fonts.length)];
    var bgColor = getHex();
    var txtColor = invertColor(bgColor);
    const axios = require('axios')

    axios.get('https://apologizator.herokuapp.com/generate', { crossDomain: true }).then(function (response) {
      console.log(response);
      document.getElementById("apology").innerHTML = response.data;
    }).then(function (response) {
      setBackground();
      document.getElementById("apology").style.color = txtColor;
      document.getElementById("apology").className = rand;
    });

    //document.getElementById("apology").innerHTML = "I am sorry for being \na little bit insanely tired";
    //document.getElementById("apology").innerHTML = axios.get('http://apologizator.herokuapp.com/generate');

  }
}

function padZero(str, len) {
  len = len || 2;
  var zeros = new Array(len).join('0');
  return (zeros + str).slice(-len);
}

function invertColor(hex, bw) {
  if (hex.indexOf('#') === 0) {
    hex = hex.slice(1);
  }
  // convert 3-digit hex to 6-digits.
  if (hex.length === 3) {
    hex = hex[0] + hex[0] + hex[1] + hex[1] + hex[2] + hex[2];
  }
  if (hex.length !== 6) {
    throw new Error('Invalid HEX color.');
  }
  var r = parseInt(hex.slice(0, 2), 16),
    g = parseInt(hex.slice(2, 4), 16),
    b = parseInt(hex.slice(4, 6), 16);
  if (bw) {
    // http://stackoverflow.com/a/3943023/112731
    return (r * 0.299 + g * 0.587 + b * 0.114) > 186
      ? '#000000'
      : '#FFFFFF';
  }
  // invert color components
  r = (255 - r).toString(16);
  g = (255 - g).toString(16);
  b = (255 - b).toString(16);
  // pad each with zeros and return
  return "#" + padZero(r) + padZero(g) + padZero(b);
}
export default App;
